.container {
   width: 80%;
   height: 100vh;
   align-items: center;
   align-content: center;
   margin: 0 auto;
}

.navbar>ul {
   display: flex;
   justify-content: space-between;
   list-style: none;
   padding-left: 0;
}

.navbar>ul>li {
   font-size: 2.1rem;
   font-weight: bold;
   padding: 5px;
   margin: auto 0;
}

.navbar hr {
   margin-bottom: 5%;
   border-color: blue;
   background-color: green;
}

.logo {
   width: fit-content;
   height: fit-content;
}

.logo img {
   width: 65px;
   height: auto;
}

.logo img:hover {
   filter: blur(50%);
   cursor: pointer;
}

.navbar button {
   background-color: lightgreen;
   border-radius: 5px;
   padding: 5px;
   font-weight: bold;
}

.main {
   text-align: center;
}

.pages {
   display: grid;
   grid-template-columns: max-content auto;
   grid-column-gap: 15px;
}


.nums {
   display: flex;
   flex-flow: wrap;
   text-align: center;
   margin: auto 0;
   
}

.page-num {
   margin: 0px 2px;
}

.page-num:hover {
   text-decoration: underline;
   cursor: pointer;
}

.file-row {
   width: 100%;
   border: 2px black solid;
   border-radius: 10px;
   margin-bottom: 5px;
}

.unnested {
   display: flex;
   flex-flow: wrap;
}


.file-row .file-info {
   margin: 5px 15px;
   display: grid;
   text-align: center;
   grid-template-columns: auto auto;
   grid-column-gap: 15px;
}

.file-row .file-info .info-key {
   text-transform: capitalize;
   text-decoration: underline;
   font-weight: bold;
   font-size: 1.2em;
}

.file-row .file-info .info-data {
   font-size: 1.15em;
}

.sub-data>div> ul {
   font-size: 1.3em;
   font-weight: bold;
   display: flex;
   justify-content: space-between;
   margin: auto 10px;
   list-style-type: none;
}

.sub-data>div> ul li {
   color: forestgreen;
   text-decoration: underline;
   transition-property: color;
   transition-duration: 500ms;
}

.sub-data>div>ul li:hover {
   color: lightblue;
   cursor: pointer;
}

.data-sub {
   width: 100%;
   display: grid;
   grid-auto-flow: column;
   grid-template-columns: min-content auto;
   grid-column-gap: 20px;
   text-align: left;
   border-top: 2px solid black;
}

.sub-key {
   margin-left: 1em;
   font-size: 1.5em;
   font-weight: bold;
   text-transform: capitalize;
   background-color: cornflowerblue;
}

div > section > span:first-child {
   text-transform: capitalize;
   font-weight: 700;
}

.data-sub-key section {
   width: auto;
   display: grid;
   grid-template-columns: min-content auto;
   grid-column-gap: 5px;
   border-top: 2px black solid;
   border-bottom: 2px black solid;
   margin: 2px 0;
}

.data-sub-key:hover {
   background-color: black;
   color: white;
}

.hidden {
   display: none;
}

.data-sub-key section span {
   text-align: left;
}

.go-blue{
   background-color: blue;
}

.go-red{
   background-color: red;
}
.selected {
   text-decoration: lightcoral;
}